<template>
	<div class="order_list">
    <van-nav-bar
      title="收益记录"
      left-text="返回"
      right-text=""
      left-arrow
      @click-left="goBack"
    />
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
    <div v-for="(el, i) in items" :key="i" class="row-item">
      <div class="row-top">
          <div class="title" :class="el.operate ==1 ? 'red': 'green'">{{el.operate ==1 ? "+" + el.exc_money : el.exc_money}}</div>
          <div class="time">{{el.create_time}}</div>
      </div>
      <div class="row-desc">
          {{el.desc}}
      </div>
    </div>
    </van-list>
	</div>
</template>

<script>
import { EARNINGS_LIST } from '@/api/user';
import { Tab, Tabs, Panel, Card, Toast } from 'vant';
export default {
		data() {
			return {
        perPage: 20,
        currPage: 1,
        items: [],
        loading: false,
        finished: false
			}
		},
		created() {

		},
		methods: {
      onLoad(){
        // 异步更新数据
        setTimeout(() => {
          this.$reqGet(EARNINGS_LIST, {
            'per_page': this.perPage,
            'page': this.currPage
          }, {
            hideLoading: true
          }).then(res => {
            if(res.data.code ==10){
              this.$router.push({path: '/user'});
              return;
            }
            // 加载状态结束
            this.loading = false;
            // 数据全部加载完成
            if(res.data.data === null || res.data.data === 'null'){
              this.finished = true;
              return;
            }
            let list = res.data.data.list;
            this.items.push(...list);
            this.currPage++;
          }).catch(err => {

          });
        }, 500);
      },
      //返回
      goBack() {
        this.$router.push({path: '/user'});
      }
		},
		components: {
			[Panel.name]: Panel,
		}
	}

</script>

<style lang="scss" scoped>
  .row-item{
    background: #fff;
    padding: 12px;
    margin-top: 5px;
  }
  .row-top{
    display: flex;
    .title {
      flex: 1;
    }
    .time{
      flex: 1;
      text-align: right;
      color: #999;
    }
    .red{
      color:red;
    }
    .green{
      color: #03ba2a;
    }
  }
  .row-desc{
    margin-top: 5px;
    word-wrap: break-word;
    word-break: break-all;
    overflow: hidden;
    color: #999;
  }

</style>



